import "./Header.css";
import { Link } from "react-router-dom";
import React, { useState } from "react";

export const Header = () => {
  const [display, setDisplay] = useState(false);

  const handleClick = () => {
    setDisplay(!display);
  };

  return (
    <header>
      <div className="navBar">
        <div className="logoContainer">
          <h1 className="logo">MUNDOSTAR</h1>
        </div>
        <div className="navTextContainer">
          <li>
            <Link exact className="link" to="/">
              Home
            </Link>
          </li>
          <li>
            <Link className="link" to="/about">
              About us
            </Link>
          </li>
          <li>
            <Link className="link" to="/ourteam">
              Our Team
            </Link>
          </li>
          <li>
            <Link className="link" to="/contact">
              Contact Us
            </Link>
          </li>
        </div>
        <button
          onClick={handleClick}
          className={
            !display ? "navBarMobile active" : "navBarMobile nonActive"
          }
        >
          <li id={!display ? "line" : "line-1"}></li>
          <li id={!display ? "line" : "line-2"}></li>
          <li id={!display ? "line" : "line-3"}></li>
        </button>
      </div>

      <div
        className={
          !display
            ? "navTextContainerMobile nonActiveMobile"
            : "navTextContainerMobile"
        }
      >
        <li>
          <Link exact onClick={handleClick} className="iconMobile" to="/">
            Home
          </Link>
        </li>
        <li>
          <Link onClick={handleClick} className="iconMobile" to="/about">
            About us
          </Link>
        </li>
        <li>
          <Link onClick={handleClick} className="iconMobile" to="/ourteam">
            Our Team
          </Link>
        </li>
        <li>
          <Link onClick={handleClick} className="iconMobile" to="/contact">
            Contact Us
          </Link>
        </li>
      </div>
    </header>
  );
};
