import React from "react";
import "./About.css";
import office from "../../../resources/office.jpg";

export const About = () => {
  return (
    <section className="who">
      <div className="contTitle">
        <h1 className="title1">Who we are?</h1>
        <p className="para1">
          Mundostar, S.A. Was founded in 1992 with the idea of provide finantial
          and processing services offshore for ITAU UNIBANCO, since that time,
          we have been characterized for the quality and time response to our
          clients.
        </p>
      </div>
      <div className="contImg">
        <img className="office" src={office} alt="Mundostar" />
      </div>
    </section>
  );
};
