import React from "react";
import "./Contact.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEnvelopeSquare, faPhone } from "@fortawesome/free-solid-svg-icons";
import { faLinkedin } from "@fortawesome/free-brands-svg-icons";

export const Contact = () => {
  return (
    <section className="contact">
      <div className="contact-card" id="phone">
        <a href="contac.html">
          <FontAwesomeIcon className="i" icon={faPhone} />
        </a>
        <h1>Phone Number:</h1>
        <p>Call us: 093292939939</p>
      </div>
      <div className="contact-card" id="email">
        <a href="/Html/contac.html">
          <FontAwesomeIcon className="i" icon={faEnvelopeSquare} />
        </a>
        <h1>Email:</h1>
        <p>Mundostar@.comasndandnna</p>
      </div>

      <div className="contact-card" id="linkedIn">
        <a href="/Html/contac.html">
          <FontAwesomeIcon className="i" icon={faLinkedin} />
        </a>
        <h1>LinkedIn:</h1>
        <p>Follow us: Mundostar@Linkeind</p>
      </div>
    </section>
  );
};
