import React from "react";
import "./Ourteam.css";
import worker from "../../../resources/worker.jpg";

export const Ourteam = () => {
  const workers = [
    {
      img: worker,
      name: "marta Dos santos",
      jobTitle: "Processing Analyst",
      reportTo: "jorge",
    },
    {
      img: worker,
      name: "Marco Sanchez",
      jobTitle: "Processing Analyst",
      reportTo: "Francis",
    },
    {
      img: worker,
      name: "Maria Ines ",
      jobTitle: "Processing Analyst",
      reportTo: "jorge",
    },
    {
      img: worker,
      name: "Maria Ines ",
      jobTitle: "Processing Analyst",
      reportTo: "jorge",
    },
    {
      img: worker,
      name: "Maria Ines ",
      jobTitle: "Processing Analyst",
      reportTo: "jorge",
    },
    {
      img: worker,
      name: "Maria Ines ",
      jobTitle: "Processing Analyst",
      reportTo: "jorge",
    },
    {
      img: worker,
      name: "Maria Ines ",
      jobTitle: "Processing Analyst",
      reportTo: "jorge",
    },
    {
      img: worker,
      name: "Maria Ines ",
      jobTitle: "Processing Analyst",
      reportTo: "jorge",
    },
  ];

  const workerMaper = workers.map((worker, i) => {
    return (
      <div className="workerStyle" key={i}>
        <img className="img" src={worker.img} alt="worker" />
        <h3 className="text">{worker.name}</h3>
        <p className="text">{worker.jobTitle}</p>
      </div>
    );
  });

  return (
    <section className="staff">
      <div className="containerFlexManager">
        <img className="img" src={worker} alt="worker" />
        <h3 className="text">Jorgue Marrizcurrena</h3>
        <p className="text">Manager</p>
      </div>
      <div className="containerFlexCord">
        <div className="cord">
          <img className="img" src={worker} alt="worker" />
          <h3 className="text">Albina Izquiero</h3>
          <p className="text">Manager</p>
        </div>
        <div className="cord">
          <img className="img" src={worker} alt="worker" />
          <h3 className="text">Claudio Yamamoto</h3>
          <p className="text">Manager</p>
        </div>
        <div className="cord">
          <img className="img" src={worker} alt="worker" />
          <h3 className="text">Paula Espindola</h3>
          <p className="text">Manager</p>
        </div>
      </div>
      <div className="containerFlexWorker" id="worker">
        {workerMaper}
      </div>
    </section>
  );
};
